/* src/components/Main.css */

body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
background: linear-gradient(45deg, #a9dc76, #3f6ea0, #4b6378, #a6b2bf, #d85c64);
    color: #ecf0f1; 
}

.App {
    text-align: center;
    color: #ecf0f1;
    background-color:linear-gradient(45deg,#0f2862, #091f36, #4f5f76);; 
    padding: 20px;
    border-radius: 10px;
}

header {
    background-color: #577594; /* Blue Popsicle */
    color: #ffffff;
    padding: 20px;
    border-bottom: 2px solid white; /* Redline border */
}

footer {
    background-color: #577594; 
    color: #ecf0f1; 
    padding: 10px;
    border-top: 2px solid #eff1f3; 
    border-radius: 10vw;
    font-family:Arial, Helvetica, sans-serif;
    font-size: large;
    
}

