#contact {
  text-align: center;
  padding: 2rem;
  background-size: cover;
  color: white;
  text-shadow: 1px 1px 2px black;
   font-family:Arial, Helvetica, sans-serif;
    font-size: large;
}

form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 0 auto;
}

input,
textarea {
  margin: 10px 0;
  padding: 10px;
  border-radius: 5vw;
  border: 2px solid #f59595;
  width: 100%; /* Ensure inputs take the full width */
  box-sizing: border-box; /* Include padding and border in the element's total width and height */
}

button {
  padding: 10px;
  border: none;
  border-radius: 5vw;
  background-color:#8adc33;
  color: white;
  cursor: pointer;
  width: 100%; /* Ensure button takes the full width */
  margin: 10px 0; /* Add margin to space it out */
  box-sizing: border-box; /* Include padding and border in the element's total width and height */
}

button:hover {
  background: linear-gradient(45deg,#8adc33,#0f2862, #091f36, #4f5f76,#9e363a); 
  color: white;
}

.contact-info {
  margin-top: 2rem;
}

.social-links a {
  margin: 0 15px;
  color: #8adc33;
  font-size: 25px;
}

.social-links a:hover {
  color: #dc25ba;
}

.footer {
  margin-top: 2rem;
  font-size: 14px;
  color: white;
  border-radius: 25;
}

/* Media Queries for Mobile Responsiveness */
@media (max-width: 600px) {
  #contact {
    padding: 1rem;
  }

  form {
    max-width: 100%;
    padding: 0 1rem;
  }

  input,
  textarea {
    width: 100%;
    padding: 12px;
    margin: 8px 0;
  }

  button {
    width: 100%;
    padding: 12px;
    margin: 8px 0; /* Ensure spacing is consistent on mobile */
  }

  .social-links a {
    font-size: 18px;
    margin: 0 5px;
  }

  .footer {
    font-size: 12px;
  }
}
